import React from "react"
import Layout from "../components/Layout"
import { graphql, Link } from "gatsby"
import Title from "../components/Title"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { SEO } from "../components/SEO"


export const query = graphql`
{
    contentfulAbout {
        title
        stack
        image {
            gatsbyImageData(layout: CONSTRAINED)
        }
        info {
            info
        }
    }
}
`

const About = ({data}) => {
    // const{title,info,stack} = nodes[0]
    const image=getImage (data.contentfulAbout.image)
    return (
    <Layout>
    <section className="about-page">
        <div className="section-center about-center">
        <GatsbyImage image={image} alt="" className="about-img" />
        <article className="about-text">
            <Title title ={data.contentfulAbout.title}/>
            <p>{data.contentfulAbout.info.info}</p>
            <div className="about-stack">
            {data.contentfulAbout.stack.map(stack => (
                <span>{stack}</span>
            ))}
            </div>
            
        </article>
        
        </div>
        <a href="https://github.com/NatMota/natmota.github.io/blob/master/Natanael%20-%20CV.pdf" className="btn center-btn" >
        view my cv</a>
    </section>
    </Layout>
    )
}

export default About

export const Head = () => (
    <SEO />
)